/* Imports ----------------------------------------------------------------- */

import "whatwg-fetch";
import { json } from "d3-fetch";
import * as L from "leaflet";

/* Functions --------------------------------------------------------------- */

function MapView(map, showMore) {

    this.map = map;
    this.showMore = showMore;
    this.zoomControl = null;
    this.tileLayer = null;
    this.areasLayer = null;
    this.zooming = false;

    // Mapbox tileLayer settings {id}
    const tileLayerPath = "https://api.mapbox.com/styles/v1/hawkinsohocl/ckf5ebbda2jbj19pf1dnn3a7h/tiles/256/{z}/{x}/{y}@2x?access_token=pk.eyJ1IjoiaGF3a2luc29ob2NsIiwiYSI6ImNrZnNqM3Z4eTBhdTAycG1qNXl2YXkyZHoifQ.AdNCbjnryr4sGhep_NC3rg";
    const tileLayerAttribution = "© <a href=\"https://www.mapbox.com/about/maps/\">Mapbox</a> | © <a href=\"https://www.openstreetmap.org/about/\">OpenStreetMap</a> | <a href=\"https://apps.mapbox.com/feedback/#/-74.5/40/10\">Improve this map</a>";
    const tileLayerAccessToken = "pk.eyJ1IjoiaGF3a2luc29ob2NsIiwiYSI6ImNrZnNqM3Z4eTBhdTAycG1qNXl2YXkyZHoifQ.AdNCbjnryr4sGhep_NC3rg";
    const tileLayerMinZoom = 5;
    const tileLayerMaxZoom = 12;

    this.tileLayer = L.tileLayer(tileLayerPath, {
        attribution: tileLayerAttribution,
        accessToken: tileLayerAccessToken,
        minZoom: tileLayerMinZoom,
        maxZoom: tileLayerMaxZoom
    });

    this.map.attributionControl.setPrefix(null);

    // Set the map to update the query string with the current position
    this.map.on("moveend", () => {
        this.setQueryString();
    });

    this.map.on("zoomstart", () => {
        this.zooming = true;
    });

    this.map.on("zoomend", () => {
        this.zooming = false;
    });

    // Sets up the map from the downloaded data
    this.setupMap = (areasLayer) => {

        this.setInfoboxHtml();
        this.areasLayer = areasLayer;
        this.areasLayer.setZIndex(1);

        this.tileLayer.addTo(this.map);
        this.areasLayer.addTo(this.map);

        this.zoomControl = L.control.zoom({position: "topright"});
        this.zoomControl.addTo(this.map);
    };

    this.setQueryString = () => {
        const c = this.map.getCenter();
        const z = this.map.getZoom();
        const s = this.showMore;
        const url = `?long=${c.lng}&lat=${c.lat}&zoom=${z}&showmore=${s}`;
        history.replaceState(null, null, url);
    };

    this.setInfoboxHtml = () => {

        let buttonId;
        let handler;
        let info;

        const getHandler = (showMore) => {
            return () => {
                this.showMore = showMore;
                this.setInfoboxHtml();
                this.setQueryString();
            };
        };

        if (this.showMore === 1) {

            buttonId = "showlessbutton";
            handler = (getHandler(0));
            info = `
                <p class="header">Coronavirus restrictions</p>
                <p>Last updated: 1 October.</p>
                <p>This map shows areas with local coronavirus restrictions.</p>
                <p>Click on an area to see a summary of the restrictions and a link to further details.</p>
                <p>You can find more information in our <a href="https://commonslibrary.parliament.uk/research-briefings/cbp-8875/">research briefing</a>.</p>
                <p>Some additional local measures may apply in Scotland.</p>
                <button id="${buttonId}" type="button">Show less</button>
            `;

        } else {

            buttonId = "showmorebutton";
            handler = (getHandler(1));
            info = `
                <p class="header">Coronavirus restrictions</p>
                <p>Last updated: 1 October.</p>                
                <button id="${buttonId}" type="button">Show more</button>
            `;

        } 

        document.getElementById("infobox").innerHTML = info;
        const showLessButton = document.getElementById(buttonId);
        showLessButton.addEventListener("click", handler, false);
    };
}


// Constructor for the MapModel object, a singleton to manage the MapView.
function MapModel(mapView) {

    this.mapView = mapView;
    this.dataDownloads = 1;
    this.areasJson = null;
    this.areasLayer = null;

    // Sets up the initial map by loading the boundaries
    this.downloadMapData = function() {

        //
        json("coronavirus-areas-2020-10-01-1.json").then(json => {
            
            this.areasJson = json;
            
            this.areasLayer = L.geoJson(this.areasJson, {
                style: (feature) => {

                    const color = getColor(feature.properties.Country);
                    let fillOpacity = 0.75;
                    
                    if (feature.properties.restrictions === "National") { 
                        fillOpacity = 0.25;
                    }

                    return {
                        fillColor: color,
                        color: color,
                        opacity: 1,
                        weight: 1.5,
                        fillOpacity: fillOpacity,
                    };
                },
                onEachFeature: (feature, layer) => {
                    layer.on("click", (e) => {
                        
                        // Ignore this event if the map is zooming
                        if (this.mapView.zooming == true) return;

                        // Render the popup
                        const html = getPopupHtml(feature.properties);
                        this.mapView.map.openPopup(
                            html, e.latlng, {closeButton: true});
                    });
                }
            });

            this.registerDownload();
        });
    };

    // Track asynchronous downloads
    this.registerDownload = function() {
        this.dataDownloads = this.dataDownloads - 1;
        if (this.dataDownloads === 0) {
            this.mapView.setupMap(
                this.areasLayer,
            );
        }
    };
}

function start(long, lat, zoom, showmore) {

    // Set the loading message
    document.getElementById("infobox").innerHTML = "Loading ...";

    // Set up application object
    const app = {};

    // Check the parameters are sane and set to defaults if not
    const getFloatParam = (val, defaultValue) => {
        if (val == null || val == false || isNaN(parseFloat(val))) {
            return defaultValue;
        } else {
            return parseFloat(val);
        }
    };

    const longitude = getFloatParam(long, -3.1);
    const latitude = getFloatParam(lat, 54.8);
    const zoomLevel = getFloatParam(zoom, 6);
    const showMore = getFloatParam(showmore, 0);

    // Create the map
    const map = L.map("map", {
        zoomControl: false,
        maxBounds: L.latLngBounds(
            L.latLng(73.0, -60.0),
            L.latLng(25.0, 50.0))
    });

    map.setView([latitude,  longitude], zoomLevel);

    // Initialise the MVC objects
    const mapView = new MapView(map, showMore);
    const mapModel = new MapModel(mapView);

    // Add an interface to the mvc objects
    app.mapView = mapView;
    app.mapModel = mapModel;

    // Setup the map
    mapModel.downloadMapData();

    return app;
}

