function getQueryString(param) {
    const qs = window.location.search.substring(1);
    const v = qs.split("&");
    for (let i = 0; i < v.length; i++) {
        const p = v[i].split("=");
        if (p["0"] == param) return p["1"];
    }
    return null;
}


function getColor(country) {

    let color;

    switch (country) {
    case "E":
        color = "#006548";
        break;
    case "W":
        color = "#e09f00";
        break;
    case "S":
        color = "#682f7f";
        break;
    case "N":
        color = "#c23f18";
        break;
    default:
        color = "#006548";
    }

    return color;
}

function getLinkStyle(country) {

    let color;

    switch (country) {
    case "E":
        color = "green";
        break;
    case "W":
        color = "yellow";
        break;
    case "S":
        color = "purple";
        break;
    case "N":
        color = "red";
        break;
    default:
        color = "green";
    }

    return color;
}

/* Main -------------------------------------------------------------------- */

if (window.self !== window.top) {
 
    document.getElementById("infobox").innerHTML = "<p>You may not embed this map without written consent from the House of Commons Library. Please visit the map on our <a href=\"https://visual.parliament.uk/research/visualisations/coronavirus-restrictions-map/\" target=\"_top\">website</a>.</p>";

} else {

    const long = getQueryString("long");
    const lat = getQueryString("lat");
    const zoom = getQueryString("zoom");
    const showmore = getQueryString("showmore");
    window.app = start(long, lat, zoom, showmore);
}